<template>
  <div>
    <el-row :gutter="17">
      <el-form :model="searchForm">
        <el-col :span="4">
          <el-input v-model="searchForm.appartmentName" placeholder="请输入民宿名" clearable></el-input>
        </el-col>
        <el-col :span="5">
          <el-cascader
            size="large"
            :options="options"
            v-model="selectedOptions"
            @change="handleChange"
            placeholder="请选择地区"
            clearable
            style="width:300px"
          ></el-cascader>
        </el-col>
        <el-col :span="7">
          <el-input
            v-model="searchForm.beginPrice"
            placeholder="请输入最低金额"
            clearable
            style="width:200px"
          ></el-input>——
          <el-input
            v-model="searchForm.endPrice"
            placeholder="请输入最高金额"
            clearable
            style="width:200px"
          ></el-input>
        </el-col>
        <el-col :span="1">
          <el-button icon="el-icon-search" circle @click="search"></el-button>
        </el-col>
        <el-col :span="1">
          <el-button icon="el-icon-plus" circle @click="add"></el-button>
          <el-dialog
            title="新增民宿"
            id="addAppartment"
            :visible.sync="addAppartmentDialog"
            center
            :close-on-click-modal="false"
          >
            <el-form :model="addForm">
              <el-row :gutter="30">
                <el-col :span="7">
                  <el-form-item label="民宿名">
                    <el-input v-model="addForm.appartmentName" clearable></el-input>
                  </el-form-item>
                </el-col>
                <el-col :span="7">
                  <el-form-item label="价格">
                    <el-input v-model="addForm.price" clearable></el-input>
                  </el-form-item>
                </el-col>
                <el-col :span="7">
                  <el-form-item label="民宿类别">
                    <el-select
                      v-model="addForm.classId"
                      clearable
                      @change="getAttr(addForm.classId)"
                    >
                      <el-option
                        v-for="item in classes"
                        :key="item.classId"
                        :label="item.className"
                        :value="item.classId"
                      ></el-option>
                    </el-select>
                  </el-form-item>
                </el-col>
              </el-row>
              <el-row :gutter="30">
                <el-col :span="7">
                  <el-form-item label="民宿类型">
                    <el-select v-model="addForm.typeId" clearable>
                      <el-option
                        v-for="item in type"
                        :key="item.typeId"
                        :label="item.typeName"
                        :value="item.typeId"
                      ></el-option>
                    </el-select>
                  </el-form-item>
                </el-col>
                <el-col :span="7">
                  <el-form-item label="民宿属性">
                    <el-select
                      v-model="addForm.attrId"
                      clearable
                      @change="getValues(addForm.attrId)"
                    >
                      <el-option
                        v-for="item in attr"
                        :key="item.attrId"
                        :label="item.attrName"
                        :value="item.attrId"
                      ></el-option>
                    </el-select>
                  </el-form-item>
                </el-col>
                <el-col :span="7">
                  <el-form-item label="特殊风格">
                    <el-select v-model="addForm.valId" clearable multiple filterable allow-create>
                      <el-option
                        v-for="item in values"
                        :key="item.valueId"
                        :label="item.valueName"
                        :value="item.valueId"
                      ></el-option>
                    </el-select>
                  </el-form-item>
                </el-col>
              </el-row>
              <el-row :gutter="30">
                <el-col :span="7">
                  <el-form-item label="请选择地区">
                    <el-cascader
                      size="large"
                      :options="areas"
                      @change="changeAreas"
                      v-model="selectedAreas"
                    ></el-cascader>
                  </el-form-item>
                </el-col>
                <el-col :span="7">
                  <el-form-item label="请输入详细地址">
                    <el-input v-model="addForm.stree" clearable></el-input>
                  </el-form-item>
                </el-col>
                <el-col :span="6">
                  <el-form-item label="房主">
                    <el-select v-model="addForm.owner" clearable>
                      <el-option
                        v-for="item in owners"
                        :key="item.accountId"
                        :label="item.accountName"
                        :value="item.accountId"
                      ></el-option>
                    </el-select>
                  </el-form-item>
                </el-col>
              </el-row>
            </el-form>
            <div slot="footer" class="dialog-footer">
              <el-button @click="closeShowInfo">关闭</el-button>
              <el-button @click="addBtn" type="primary">确认</el-button>
            </div>
          </el-dialog>
        </el-col>
        <el-col :span="3">
          <el-button icon="el-icon-edit" @click="modifyPic">照片管理</el-button>
          <el-dialog title="照片管理" :visible.sync="picManagerDialog">
            <el-row>
              <el-col :span="8">
                <span>客厅</span>
                <el-upload
                  :action="'http://localhost/picture/upload?appartmentId='+currentRow.apartmentId+'&type=Parlour'"
                  list-type="picture-card"
                  :on-preview="handleParlourPreview"
                  :on-remove="handleRemove"
                  :on-success="success"
                >
                  <i class="el-icon-plus"></i>
                </el-upload>
                <el-dialog :visible.sync="dialogParlourVisible">
                  <img width="100%" :src="dialogParlourUrl" alt>
                </el-dialog>
              </el-col>
              <el-col :span="8">
                <span>厨房</span>
                <el-upload
                  :action="'http://localhost/picture/upload?appartmentId='+currentRow.apartmentId+'&type=Kitchen'"
                  list-type="picture-card"
                  :on-preview="handleKitchenPreview"
                  :on-success="success"
                  :on-remove="handleRemove"
                >
                  <i class="el-icon-plus"></i>
                </el-upload>
                <el-dialog :visible.sync="dialogKitchenVisible">
                  <img width="100%" :src="dialogKitchenUrl" alt>
                </el-dialog>
              </el-col>
              <el-col :span="8">
                <span>卧室</span>
                <el-upload
                  :action="'http://localhost/picture/upload?appartmentId='+currentRow.apartmentId+'&type=Bedroom'"
                  list-type="picture-card"
                  :on-preview="handleBedroomPreview"
                  :on-success="success"
                  :on-remove="handleRemove"
                >
                  <i class="el-icon-plus"></i>
                </el-upload>
                <el-dialog :visible.sync="dialogBedroomVisible">
                  <img width="100%" :src="dialogBedroomUrl" alt>
                </el-dialog>
              </el-col>
            </el-row>
            
          </el-dialog>
        </el-col>
        <el-col :span="3">
          <el-button @click="cancleClick()">取消选择</el-button>
        </el-col>
      </el-form>
    </el-row>
    <el-row>
      <el-table
        ref="singleTable"
        :data="tableData"
        border
        style="width:100%"
        v-loading="loading"
        v-bind:element-loading-text="loadingText"
        element-loading-background="rgba(0, 0, 0, 0.8)"
        highlight-current-row
        @current-change="tableClick"
      >
        <el-table-column prop="apartmentId" label="民宿id" align="center" width="100px" sortable></el-table-column>
        <el-table-column prop="apartmentName" label="民宿名" align="center" width="200px"></el-table-column>
        <el-table-column prop="price" label="单价" align="center" width="100px"></el-table-column>
        <el-table-column prop="address" label="地址" align="center"></el-table-column>
        <el-table-column label="操作" align="center">
          <template slot-scope="scope">
            <el-button
              size="mini"
              slot="reference"
              icon="el-icon-info"
              type="info"
              @click="handleShowInfo(scope.$index,scope.row)"
            >查看</el-button>
            <el-dialog
              title="详细信息"
              id="showInfo"
              :visible.sync="showInfoDialog"
              center
              :close-on-click-modal="false"
              v-loading="showInfoLoading"
              v-bind:element-loading-text="loadingText"
              element-loading-background="rgba(0, 0, 0, 0.8)"
            >
              <el-form :model="showInfo">
                <el-form-item label="民宿名">
                  <el-input v-model="showInfo.appartmentName" disabled></el-input>
                </el-form-item>
                <el-form-item label="民宿类型">
                  <el-input v-model="showInfo.typeName" disabled></el-input>
                </el-form-item>
                <el-form-item label="民宿属性">
                  <el-input v-model="showInfo.attrName" disabled></el-input>
                </el-form-item>
                <el-form-item label="民宿类别">
                  <el-input v-model="showInfo.className" disabled></el-input>
                </el-form-item>
                <el-form-item label="民宿地点">
                  <el-input v-model="showInfo.address" disabled></el-input>
                </el-form-item>
                <el-form-item label="价格">
                  <el-input v-model="showInfo.price" disabled></el-input>
                </el-form-item>
                <el-form-item label="特殊功能">
                  <el-input v-model="showInfo.valueName" disabled></el-input>
                </el-form-item>
              </el-form>
              <div slot="footer" class="dialog-footer">
                <el-button @click="closeShowInfo">关闭</el-button>
              </div>
            </el-dialog>&nbsp;
            <el-button
              size="mini"
              icon="el-icon-edit"
              @click="handleEdit(scope.$index,scope.row)"
            >编辑</el-button>
            <el-dialog title="编辑" :visible.sync="editDialog">
              <el-form :model="editForm">
                <el-form-item label="修改民宿名">
                  <el-input v-model="editForm.appartmentName"></el-input>
                </el-form-item>
                <el-form-item label="修改价格">
                  <el-input v-model="editForm.price"></el-input>
                </el-form-item>
              </el-form>
              <div slot="footer" class="dialog-footer">
                <el-button @click="cancelModify">取消</el-button>
                <el-button @click="confirmModify" type="primary">确定</el-button>
              </div>
            </el-dialog>&nbsp;
            <el-button
              size="mini"
              icon="el-icon-delete"
              type="danger"
              @click="handleDelete(scope.$index,tableData,scope.row)"
            >删除</el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-row>
    <el-row>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-sizes="[5, 10]"
        :page-size="5"
        layout="total, sizes, prev, pager, next, jumper"
        v-bind:total="total"
      ></el-pagination>
    </el-row>
  </div>
</template>

<script>
import { regionDataPlus, CodeToText } from "element-china-area-data";
import appartmentRender from "../../../api/appartmentRender.js";
import tableRender from "../../../api/tableRender.js";
export default {
  name: "room",
  data() {
    return {
      loading: true,
      loadingText: "加载中……",
      selectedOptions: [],
      options: regionDataPlus,
      areas: regionDataPlus,
      pageSize: "",
      total: 0,
      currentPage: 1,
      showInfoDialog: false,
      showInfoLoading: false,
      editDialog: false,
      addAppartmentDialog: false,
      picManagerDialog: false,
      selectedAreas: [],
      addForm: {
        appartmentName: "",
        price: "",
        classId: "",
        typeId: "",
        attrId: "",
        valId: "",
        selectedAreas: "",
        stree: "",
        address: "",
        owner: ""
      },
      classes: [
        {
          classId: "",
          className: ""
        }
      ],
      type: [
        {
          typeId: "",
          typeName: ""
        }
      ],
      attr: [
        {
          attrId: "0",
          attrName: "请先选择类别"
        }
      ],
      values: [
        {
          valueId: "0",
          valueName: "请先选择属性"
        }
      ],
      owners: [
        {
          accountId: "",
          accountName: ""
        }
      ],
      tableData: [
        {
          apartmentId: "",
          apartmentName: "",
          price: "",
          address: "",
          name: ""
        }
      ],
      searchForm: {
        appartmentName: "",
        address: "",
        beginPrice: "",
        endPrice: "",
        attrIds:[],
        typeIds:[]
      },
      showInfo: {
        apartmentName: "",
        typeName: "",
        attrName: "",
        className: "",
        address: "",
        price: "",
        valueName: ""
      },
      editForm: {
        appartmentId: "",
        appartmentName: "",
        price: ""
      },
      currentRow: {
        apartmentId: "",
        appartmentName: "",
        typeName: "",
        price: "",
        address: "",
        name: ""
      },
      dialogParlourVisible: false,
      dialogKitchenVisible: false,
      dialogBedroomVisible: false,
      dialogParlourUrl: "",
      dialogKitchenUrl: "",
      dialogBedroomUrl: ""
    };
  },
  methods: {
    //searchForm
    search() {
      window.console.log("search");
      var address = "";
      let that = this;
      that.loading = true;
      that.loadingText = "搜索中……";
      for (var i = 0; i < this.selectedOptions.length; i++) {
        if ("全部" === CodeToText[this.selectedOptions[i]]) {
          address += "";
        } else {
          address += CodeToText[this.selectedOptions[i]];
        }
      }
      this.searchForm.address = address;
      appartmentRender
        .findAllAppartments(that.pageSize, 1, that.searchForm)
        .then(function(res) {
          var data = res.data.data;
          var total = res.data.count;
          that.total = total;
          that.tableData = data;
          that.loading = false;
        });
    },
    getAttr(classId) {
      window.console.log(classId);
      let that = this;
      appartmentRender.getAllAttr(classId).then(function(res) {
        var data = res.data;
        that.attr = data;
      });
    },
    getValues(attrId) {
      window.console.log(attrId);
      let that = this;
      appartmentRender.getValues(attrId).then(function(res) {
        var data = res.data;
        that.values = data;
      });
    },
    add() {
      window.console.log("add");
      this.addAppartmentDialog = true;
      let that = this;
      appartmentRender.getAllClass().then(function(res) {
        window.console.log(res.data);
        that.classes = res.data;
      });
      appartmentRender.getAllType().then(function(res) {
        window.console.log(res.data);
        that.type = res.data;
      });
      tableRender.fidndAllUsers(2, 5, 1, null, null, null).then(function(res) {
        window.console.log(res.data.data);
        that.owners = res.data.data;
      });
    },
    changeAreas(value) {
      window.console.log(value);
      this.selectedAreas = value;
    },
    addBtn() {
      window.console.log("add");
      var address = "";
      for (var i = 0; i < this.selectedAreas.length; i++) {
        if ("全部" === CodeToText[this.selectedAreas[i]]) {
          address += "";
        } else {
          address += CodeToText[this.selectedAreas[i]];
        }
      }
      address += this.addForm.stree;
      this.addForm.address = address;
      window.console.log(this.addForm);
      let that = this;
      appartmentRender.add(this.addForm).then(function(res) {
        window.console.log(res);
        if (res.data <= 0) {
          that.$message.error("新增失败,请重试");
        } else {
          that.addAppartmentDialog = false;
          that.loadingText = "新增中……";
          that.loading = true;
          that.handleCurrentChange(that.currentPage);
          that.$message({
            type: "success",
            message: "新增成功"
          });
        }
      });
    },
    //table
    getAllAppartments() {
      let that = this;
      appartmentRender
        .findAllAppartments(5, 1, this.searchForm)
        .then(function(res) {
          var data = res.data.data;
          that.tableData = data;
          that.loading = false;
          that.total = res.data.count;
        });
    },
    tableClick(val) {
      this.currentRow = val;
      window.console.log(this.currentRow);
    },
    handleSizeChange(pageSize) {
      window.console.log(pageSize);
      let that = this;
      that.loading = true;
      appartmentRender
        .findAllAppartments(pageSize, 1, this.searchForm)
        .then(function(res) {
          var data = res.data.data;
          var total = res.data.count;
          that.tableData = data;
          that.total = total;
          that.loading = false;
        });
    },
    handleCurrentChange(val) {
      let that = this;
      that.loading = true;
      window.console.log("当前页：", val);
      window.console.log("页面大小:", that.pageSize);
      appartmentRender
        .findAllAppartments(that.pageSize, val, that.searchForm)
        .then(function(res) {
          var data = res.data.data;
          var total = res.data.count;
          that.tableData = data;
          that.total = total;
          that.currentPage = val;
          that.loading = false;
        });
    },
    handleShowInfo(index, row) {
      window.console.log("查看");
      this.showInfoLoading = true;
      this.showInfoDialog = true;
      this.loadingText = "加载中……";
      let that = this;
      appartmentRender
        .getInfoByAppartmentId(row.apartmentId)
        .then(function(res) {
          window.console.log(row.apartmentId);
          window.console.log(res);
          that.showInfo = res.data;
          that.showInfoLoading = false;
        });
    },
    closeShowInfo() {
      this.showInfoDialog = false;
      this.addAppartmentDialog = false;
    },
    handleEdit(index, row) {
      window.console.log("编辑");
      window.console.log("index==>", index);
      window.console.log("row==>", row);
      this.editDialog = true;
      this.editForm.appartmentName = row.apartmentName;
      this.editForm.price = row.price;
      this.editForm.appartmentId = row.apartmentId;
    },
    handleDelete(index, rows, row) {
      window.console.log("删除");
      window.console.log("index==>", index);
      this.$confirm("确定删除？", "确认", {
        confirmButtonText: "删除",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          rows.splice(index, 1);
          window.console.log(row.apartmentId);
          appartmentRender.deleteByPrimaryKey(row.apartmentId);
          this.$message({
            type: "success",
            message: "删除成功！"
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },
    handleChange(value) {
      window.console.log(value);
      this.selectedOptions = value;
    },
    confirmModify() {
      window.console.log("修改");
      window.console.log(this.editForm);
      let that = this;
      appartmentRender.update(this.editForm).then(function(res) {
        window.console.log(res.data);
        that.loading = true;
        that.loadingText = "更新中……";
        that.getAllAppartments(5, 1, that.searchForm);
        if (res.data === 1) {
          that.$message({
            message: "修改成功",
            type: "success"
          });
        } else {
          that.$message.error("修改失败");
        }
        that.handleCurrentChange(that.currentPage);
        that.editDialog = false;
      });
    },
    cancelModify() {
      window.console.log("取消");
      this.$message("取消修改");
      this.editDialog = false;
    },
    modifyPic() {
      window.console.log("照片管理");
      if (null != this.currentRow.apartmentId && this.currentRow.apartmentId != "") {
        this.picManagerDialog = true;
      } else {
        this.$notify.error({
          title: "错误",
          message: "请选择表格中的一列"
        });
      }
      window.console.log(this.picManagerDialog);
      window.console.log(this.currentRow);
    },
    handleParlourPreview(file) {
      window.console.log("file==>", file);
      this.dialogParlourVisible = true;
      this.dialogParlourUrl = file.url;
    },
    handleKitchenPreview(file) {
      window.console.log("file==>", file);
      this.dialogKitchenVisible = true;
      this.dialogKitchenUrl = file.url;
    },
    handleBedroomPreview(file) {
      window.console.log("file==>", file);
      this.dialogBedroomVisible = true;
      this.dialogBedroomUrl = file.url;
    },
    handleRemove(file, fileList) {
      window.console.log("fileList==>", fileList);
    },
    cancleClick(row) {
      this.$refs.singleTable.setCurrentRow(row);
    },
    success(response, file, fileList){
      window.console.log(response);
      window.console.log("file==>",file);
      window.console.log("fileList==>",fileList);
      if(response<=0){
        this.$message.error("上传失败")
      }else{
        this.$message({
          type:"success",
          message:"上传成功"
        })
      }
    }
  },
  mounted() {
    this.getAllAppartments();
  }
};
</script>


<style>
</style>