import axios from "axios"

export default{
    findAllAppartments(pageSize,pageNum,form){
        axios.defaults.withCredentials=true;
        return axios.post("http://localhost/appartment/getAll?limit="+pageSize+"&page="+pageNum,form)
    },
    
    getInfoByAppartmentId(appartmentId){
        axios.defaults.withCredentials=true;
        return axios.post("http://localhost/appartment/getInfoByAppartmentId?appartmentId="+appartmentId)
    },
    deleteByPrimaryKey(appartmentId){
        axios.defaults.withCredentials=true;
        return axios.post("http://localhost/appartment/deleteByPrimaryKey?appartmentId="+appartmentId)    
    },
    update(appartment){
        axios.defaults.withCredentials=true;
        return axios.post("http://localhost/appartment/update",appartment);
    },
    getAllClass(){
        axios.defaults.withCredentials=true;
        return axios.post("http://localhost/appartmentClass/getAll");
    },
    getAllType(){
        axios.defaults.withCredentials=true;
        return axios.post("http://localhost/appartmentType/getAll");
    },
    getAllAttr(classId){
        axios.defaults.withCredentials=true;
        return axios.post("http://localhost/appartmentAttr/getAll?classId="+classId);
    },
    getValues(attrId){
        axios.defaults.withCredentials=true;
        return axios.post("http://localhost/value/getValues?attrId="+attrId);
    },
    add(addForm){
        axios.defaults.withCredentials=true;
        return axios.post("http://localhost/appartment/insert",addForm);
    }
}