<template>
  <div>
    <el-row :gutter="30">
      <el-col :span="12">
        <el-card class="box-card">
          <div slot="header" class="clearfix">
            <span>年度业绩</span>
          </div>
          <ve-line :data="yearCharData"></ve-line>
        </el-card>
      </el-col>
      <el-col :span="12">
        <el-card class="box-card">
          <div slot="header" class="clearfix">
            <span>月度业绩</span>
            <el-select v-model="value" placeholder="请选择" style="float: right;" @change="change">
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value"
                
              ></el-option>
            </el-select>
            <ve-line :data="monthCharData"></ve-line>
          </div>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import index from "../../api/index.js";
export default {
  name: "index",
  data() {
    return {
      options: [
        {
          value: "",
          label: ""
        }
      ],
      value:"",
      yearCharData: {
        columns: ["year","sum"],
        rows: [{}]
      },
      monthCharData:{
          columns:["month","sum"],
          rows:[{}]
      }
    };
  },
  methods: {
    getYears() {
      let that = this;
      index.getYears().then(function(res) {
        window.console.log("res=>", res);
        var data = res.data;
        var ops=[]
        for(var i=0;i<data.length;i++){
            ops.push({value:data[i],label:data[i]})
        }
        window.console.log("ops",ops);
        var date=new Date;
        var year=date.getFullYear(); 
        that.value=year;
        that.options=ops
      });
    },
    getSumByYear(){
        let that=this;
        index.getSumByYear().then(function(res){
            window.console.log("res=>",res);
            var rs=[];
            for(var i=0;i<res.data.length;i++){
                rs.push({"year":res.data[i].year.toString(),"sum":res.data[i].sum})
            }
            window.console.log("rs",rs);
            that.yearCharData.rows=rs;
            window.console.log("yearCharData=>",that.yearCharData);

        })
    },
    getSumByMonth(year){
        let that=this;
        index.getSumByMonth(year).then(function(res){
            var rs=[];
            for(var i=0;i<res.data.length;i++){
                rs.push({"month":res.data[i].month.toString(),"sum":res.data[i].sum})
            }
            that.monthCharData.rows=rs;
        })
    },
    change(value){
        window.console.log("change",value);
        this.value=value;
        this.getSumByMonth(value);
    }
  },
  mounted() {
    this.getYears();
    this.getSumByYear();
    var date=new Date();
    var year=date.getFullYear();
    this.getSumByMonth(year);
  }
};
</script>

