import axios from "axios"

export default{
    getAll(pageSize,pageNum,searchForm){
        axios.defaults.withCredentials=true;
        return axios.post("http://localhost/order/getAll?limit="+pageSize+"&page="+pageNum,searchForm)
    },
    closeOrder(delOrder){
        axios.defaults.withCredentials=true;
        return axios.post("http://localhost/order/delete",delOrder)
    },
    update(updateForm){
        axios.defaults.withCredentials=true;
        return axios.post("http://localhost/order/update",updateForm)
    }
}
