<template>
  <div>
    <!--search-->
    <el-row>
      <el-form :model="searchForm">
        <el-col :span="6">
          <el-input placeholder="请输入账户名" v-model="searchForm.accountName" clearable></el-input>
        </el-col>
        <el-col :span="6">
          <el-select placeholder="请选择评价类型" v-model="searchForm.type" clearable>
            <el-option
              v-for="item in types"
              :key="item.typeId"
              :value="item.typeValue"
              :label="item.typeValue"
            ></el-option>
          </el-select>
        </el-col>
        <el-col :span="1">
          <el-button icon="el-icon-search" circle @click="search"></el-button>
        </el-col>
      </el-form>
    </el-row>
    <!--table-->
    <el-row>
      <el-table
        :data="tableData"
        stripe
		border
        style="width: 100%"
        v-loading="loading"
        v-bind:element-loading-text="loadingText"
        element-loading-background="rgba(0, 0, 0, 0.8)"
      >
        <el-table-column prop="messageId" label="id" align="center"></el-table-column>
        <el-table-column prop="accountName" label="账户名" align="center"></el-table-column>
        <el-table-column prop="serviceRank" label="服务等级" align="center"></el-table-column>
        <el-table-column prop="customerRank" label="客户等级" align="center"></el-table-column>
        <el-table-column prop="livingRank" label="居住等级" align="center"></el-table-column>
        <el-table-column prop="message" label="留言" align="center"></el-table-column>
        <el-table-column prop="type" label="评价类型" align="center"></el-table-column>
      </el-table>
    </el-row>
    <!--page-->
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-sizes="[5, 10]"
      :page-size="5"
      layout="total, sizes, prev, pager, next, jumper"
      v-bind:total="total"
    ></el-pagination>
  </div>
</template>

<script>
import messageRender from "../../../api/messageRender.js";
export default {
  name: "message",
  data() {
    return {
      loading: false,
      loadingText: "加载中……",
      //searchForm
      searchForm: {
        accountName: null,
        type: null
      },
      types: [
        {
          typeId: "0",
          typeValue: "服务评价"
        },
        {
          type: "1",
          typeValue: "整体评价"
        },
        {
          type:"2",
          typeValue:"顾客评价"
        }
      ],

      //table
      tableData: [
        {
          messageId: null,
          accountName: null,
          serviceRank: null,
          livingRank: null,
          message: null,
          customerRank: null,
          type: null
        }
      ],
      //page
      currentPage: 1,
      total: 0,
      pageSize: ""
    };
  },
  methods: {
    search() {
      window.console.log("搜索", this.searchForm);
      let that = this;
      this.loading = true;
      this.loadingText = "搜索中……";
      messageRender.getAll(5, 1, this.searchForm).then(function(res) {
        that.tableData = res.data.data;
        that.total = res.data.count;
		that.loading = false;
		that.loadingText="加载中……"
      });
    },
    messageTable() {
      window.console.log("表格加载");
      let that = this;
      this.loading = true;
      messageRender.getAll(5, 1, this.searchForm).then(function(res) {
        that.tableData = res.data.data;
        that.total = res.data.count;
        that.loading = false;
      });
    },
    handleSizeChange(pageSize) {
      window.console.log("改变页面大小", pageSize);
      let that = this;
      that.loading = true;
      messageRender.getAll(pageSize, 1, this.searchForm).then(function(res) {
        var data = res.data.data;
        var total = res.data.count;
        that.tableData = data;
        that.total = total;
        that.loading = false;
      });
    },
    handleCurrentChange(val) {
      window.console.log("改变页数", val);
      let that = this;
      that.loading = true;
      window.console.log("当前页：", val);
      window.console.log("页面大小:", that.pageSize);
      messageRender
        .getAll(that.pageSize, val, that.searchForm)
        .then(function(res) {
          var data = res.data.data;
          var total = res.data.count;
          that.tableData = data;
          that.total = total;
          that.currentPage = val;
          that.loading = false;
        });
    }
  },
  mounted() {
    this.messageTable();
  }
};
</script>

