import axios from 'axios'

export default{
    getYears(){
        axios.defaults.withCredentials=true;
        return axios.post("http://localhost/order/getYears")
    },

    getSumByYear(){
        axios.defaults.withCredentials=true;
        return axios.post("http://localhost/order/getSumByYear")
    },

    getSumByMonth(year){
        axios.defaults.withCredentials=true;
        return axios.post("http://localhost/order/getSumByMonth?year="+year)
    }
    
}