import axios from "axios"

export default{
    findAllUsers(role,pageSize,pageNum,accountName,name,telphone){
        axios.defaults.withCredentials=true;
        return axios.post("http://localhost/account/getAccountsByExample?limit="+pageSize+"&page="+pageNum,{
                roleId:role,
                accountName:accountName,
                name:name,
                telphone:telphone
            }
        )
    },
    modifyUsers(form){
        axios.default.withCredentials=true;
        return axios.post("http://localhost/account/modify",form)
    },
    addUser(from,roleId){
        axios.defaults.withCredentials=true;
        return axios.post("http://localhost/account/insert?roleId="+roleId,from);
    }
}   