import Vue from 'vue'
import Router from 'vue-router'
import customer from '@/components/page/accountPage/customer'
import seller from '@/components/page/accountPage/seller'
import message from '@/components/page/messagePage/message'
import order from '@/components/page/orderPage/order'
import room from '@/components/page/roomPage/room'
import index from '@/components/page/index'
import manager from '@/components/page/accountPage/manager'
Vue.use(Router)

export default new Router({
	routes: [
		{
			path:'/index',
			name:'index',
			component:index
			
		},
		{
			path: '/customer',
			name: 'customer',
			component: customer
		},
		{
			path: '/seller',
			name: 'seller',
			component: seller
		},
		{
			path: '/message',
			name: 'message',
			component: message
		},
		{
			path: '/order',
			name: 'order',
			component: order
		},
		{
			path: '/room',
			name: 'room',
			component: room
		},
		{
			path: '/manager',
			name: 'manager',
			component: manager
		}
	]

})
