<template>
  <div>
    <!--search-->
    <el-row :gutter="30">
      <el-form :model="searchForm">
        <el-col :span="6">
          <el-input placeholder="请输入订单号" clearable v-model="searchForm.orderId"></el-input>
        </el-col>
        <el-col :span="5">
          <el-select placeholder="请选择状态" clearable v-model="searchForm.status">
            <el-option
              v-for="item in statusSelect"
              :key="item.statusId"
              :value="item.statusValue"
              :label="item.statusValue"
            ></el-option>
          </el-select>
        </el-col>
        <el-col :span="1">
          <el-button icon="el-icon-search" circle @click="search"></el-button>
        </el-col>
      </el-form>
    </el-row>
    <!--table-->
    <el-row>
      <el-table
        :data="tableData"
        border
        style="width: 100%"
        v-loading="loading"
        v-bind:element-loading-text="loadingText"
        element-loading-background="rgba(0, 0, 0, 0.8)"
      >
        <el-table-column prop="orderId" label="订单id" align="center" width="150" sortable></el-table-column>
        <el-table-column prop="apartmentName" label="公寓名" align="center" width="150"></el-table-column>
        <el-table-column prop="accountName" label="账户名" align="center" width="150"></el-table-column>
        <el-table-column prop="prePay" label="定金" align="center" width="150"></el-table-column>
        <el-table-column prop="price" label="单价" align="center" width="150"></el-table-column>
        <el-table-column prop="checkInDate" label="入住时间" align="center" width="150" sortable></el-table-column>
        <el-table-column prop="checkOutDate" label="退房时间" align="center" width="150" sortable></el-table-column>
        <el-table-column prop="total" label="总额" align="center" width="150" sortable></el-table-column>
        <el-table-column prop="status" label="状态" align="center" width="150"></el-table-column>
        <el-table-column label="操作" align="center">
          <template slot-scope="scope">
            <el-button
              size="mini"
              @click="handleEdit(scope.$index, scope.row)"
              icon="el-icon-edit"
            >编辑</el-button>
            <el-dialog title="修改入住时间" :visible.sync="modifyDialogForm">
              <el-form :model="modifyForm">
                <el-date-picker v-model="modifyForm.checkInDate" type="date" placeholder="选择日期"></el-date-picker>——
                <el-date-picker v-model="modifyForm.checkOutDate" type="date" placeholder="选择日期"></el-date-picker>
              </el-form>
              <div slot="footer" class="dialog-footer">
                <el-button @click="cancleModify">取 消</el-button>
                <el-button type="primary" @click="confirmModify">确 定</el-button>
              </div>
            </el-dialog>&nbsp;
            <el-button
              size="mini"
              @click="handleDel(scope.$index, scope.row)"
              icon="el-icon-close"
            >关闭</el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-row>
    <!--page-->
    <el-row>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-sizes="[5, 10]"
        :page-size="5"
        layout="total, sizes, prev, pager, next, jumper"
        v-bind:total="total"
      ></el-pagination>
    </el-row>
  </div>
</template>

<script>
import orderRender from "../../../api/orderRender.js";
export default {
  name: "order",
  data() {
    return {
      //加载特效
      loading: false,
      loadingText: "加载中…………",
      //search
      searchForm: {
        orderId: "",
        status: null
      },
      statusSelect: [
        {
          statusId: "1",
          statusValue: "已预订"
        },
        {
          statusId: "0",
          statusValue: "已关闭"
        },
        {
          statusId: "2",
          statusValue: "已完成"
        }
      ],
      //table
      tableData: [
        {
          orderId: "",
          apartmentName: "",
          accountName: "",
          prePay: "",
          price: "",
          checkInDate: "",
          checkOutDate: "",
          total: "",
          status: ""
        }
      ],
      //modify
      modifyDialogForm: false,
      modifyForm: {
        checkInDate: "",
        checkInDate: "",
        orderId:""
      },
      //page
      currentPage: 1,
      total: 0,
      pageSize: ""
    };
  },
  methods: {
    //search
    search() {
      window.console.log("searchForm==>", this.searchForm);
      let that = this;
      this.loading = true;
      this.loadingText = "搜索中……";
      orderRender.getAll(5, 1, this.searchForm).then(function(res) {
        that.tableData = res.data.data;
        that.total = res.data.count;
        that.loading = false;
      });
    },
    //table
    orderTable() {
      let that = this;
      this.loading = true;
      orderRender.getAll(5, 1, this.searchForm).then(function(res) {
        window.console.log(res);
        that.tableData = res.data.data;
        that.total = res.data.count;
        that.loading = false;
      });
    },
    //modify
    handleEdit(index, row) {
      window.console.log("index==>", index);
      window.console.log("row==>", row);
      this.modifyDialogForm = true;
      this.modifyForm.checkInDate = row.checkInDate;
      this.modifyForm.checkOutDate = row.checkOutDate;
      this.modifyForm.orderId=row.orderId;
    },
    cancleModify() {
      this.modifyDialogForm = false;
      this.$message({
        type: "info",
        message: "已取消关闭"
      });
    },
    confirmModify() {
      let that = this;
      orderRender.update(this.modifyForm).then(function(res) {
        if (res.data == 1) {
          that.modifyDialogForm=false;
          that.loadingText="修改中……"
          that.handleCurrentChange(that.currentPage);
          that.$message({
            type: "success",
            message: "修改成功"
          });
        } else {
          that.$message.error("修改失败");
        }
      });
    },
    //close
    handleDel(index, row) {
      let that = this;
      window.console.log("index==>", index);
      window.console.log("row==>", row);
      this.$confirm("关闭订单", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          orderRender.closeOrder(row).then(function(res) {
            if (res.data == 1) {
              that.$message({
                type: "success",
                message: "关闭成功"
              });
              that.orderTable();
            }
            if (res.data <= 0) {
              that.$message.error("关闭失败");
            }
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "取消关闭"
          });
        });
    },
    //page
    handleSizeChange(pageSize) {
      window.console.log(pageSize);
      let that = this;
      that.loading = true;
      orderRender.getAll(pageSize, 1, this.searchForm).then(function(res) {
        var data = res.data.data;
        var total = res.data.count;
        that.tableData = data;
        that.total = total;
        that.loading = false;
      });
    },
    handleCurrentChange(val) {
      let that = this;
      that.loading = true;
      window.console.log("当前页：", val);
      window.console.log("页面大小:", that.pageSize);
      orderRender
        .getAll(that.pageSize, val, that.searchForm)
        .then(function(res) {
          var data = res.data.data;
          var total = res.data.count;
          that.tableData = data;
          that.total = total;
          that.currentPage = val;
          that.loading = false;
        });
    }
  },
  mounted() {
    this.orderTable();
  }
};
</script>

