<template>
  <div>
    <el-row :gutter="20">
      <el-col :span="6">
        <el-input type="text" placeholder="请输入用户名" v-model="accountName" clearable></el-input>
      </el-col>
      <el-col :span="6">
        <el-input type="text" placeholder="请输入姓名" v-model="name" clearable></el-input>
      </el-col>
      <el-col :span="6">
        <el-input type="text" placeholder="请输入电话号码" v-model="telphone" clearable></el-input>
      </el-col>
      <el-col :span="1">
        <el-button icon="el-icon-search" circle @click="search"></el-button>
      </el-col>
      <el-col :span="3">
        <el-button icon="el-icon-plus" circle @click="add"></el-button>
        <el-dialog
          title="新增管理员"
          id="modify"
          :visible.sync="addDialog"
          center
          style="top:-120px"
          :close-on-click-modal="false"
        >
          <el-form :model="formAdd" ref="formAddRule" :rules="rules">
            <el-form-item label="账户名" prop="accountName">
              <el-input v-model="formAdd.accountName"></el-input>
            </el-form-item>
            <el-form-item label="密码" prop="password">
              <el-input v-model="formAdd.password"></el-input>
            </el-form-item>
            <el-form-item label="真实姓名" prop="name">
              <el-input v-model="formAdd.name"></el-input>
            </el-form-item>
            <el-form-item label="身份证号码" prop="idNumber">
              <el-input v-model="formAdd.idNumber"></el-input>
            </el-form-item>
            <el-form-item label="性别">
              <el-radio-group v-model="formAdd.gender">
                <el-radio label="男"></el-radio>
                <el-radio label="女"></el-radio>
              </el-radio-group>
            </el-form-item>
            <el-form-item label="出生日期">
              <el-col :span="11">
                <el-date-picker
                  type="date"
                  placeholder="选择日期"
                  v-model="formAdd.birth"
                  style="width: 100%;"
                ></el-date-picker>
              </el-col>
            </el-form-item>
            <el-form-item label="电话号码" prop="telphone">
              <el-input v-model.number="formAdd.telphone"></el-input>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click="cancleAdd">取 消</el-button>
            <el-button type="primary" @click="confirmAdd('formAddRule')">确 定</el-button>
          </div>
        </el-dialog>
      </el-col>
    </el-row>
    <el-row>
      <el-table
        :data="tableData"
        border
        style="width: 100%"
        v-loading="loading"
        v-bind:element-loading-text="loadingText"
        element-loading-background="rgba(0, 0, 0, 0.8)"
        :default-sort="{prop:'accountId',order:'ascending'}"
      >
        <el-table-column prop="accountId" label="账户id" align="center" width="150" sortable></el-table-column>
        <el-table-column prop="accountName" label="账户名" align="center" width="200"></el-table-column>
        <el-table-column prop="name" label="姓名" align="center" width="200"></el-table-column>
        <el-table-column prop="gender" label="性别" align="center" width="100"></el-table-column>
        <el-table-column prop="birth" label="出生日期" align="center" width="200"></el-table-column>
        <el-table-column prop="telephone" label="电话号码" align="center" width="200"></el-table-column>
        <el-table-column prop="remainderMoney" label="余额" align="center" width="150"></el-table-column>
        <el-table-column label="操作" align="center">
          <template slot-scope="scope">
            <el-popover placement="left" title="身份证号码" trigger="click">
              <el-input v-model="idNumber" disabled></el-input>
              <el-button
                size="mini"
                slot="reference"
                @click="showIdNumber(scope.$index, scope.row)"
                icon="el-icon-info"
                type="info"
              >查看</el-button>
            </el-popover>&nbsp;
            <el-button
              size="mini"
              @click="handleEdit(scope.$index, scope.row)"
              icon="el-icon-edit"
            >编辑</el-button>
            <el-dialog
              title="修改信息"
              id="modify"
              :visible.sync="dialogFormVisible"
              center
              style="top:-120px"
              :close-on-click-modal="false"
            >
              <el-form :model="form">
                <el-form-item label="账户id">
                  <el-input v-model="form.accountId" disabled></el-input>
                </el-form-item>
                <el-form-item label="账户名">
                  <el-input v-model="form.accountName" clearable></el-input>
                </el-form-item>
                <el-form-item label="密码">
                  <el-input v-model="form.password" clearable></el-input>
                </el-form-item>
                <el-form-item label="真实姓名">
                  <el-input v-model="form.name" disabled></el-input>
                </el-form-item>
                <el-form-item label="性别">
                  <el-radio-group v-model="form.gender">
                    <el-radio label="男"></el-radio>
                    <el-radio label="女"></el-radio>
                  </el-radio-group>
                </el-form-item>
                <el-form-item label="出生日期">
                  <el-col :span="11">
                    <el-date-picker
                      type="date"
                      placeholder="选择日期"
                      v-model="form.birth"
                      style="width: 100%;"
                      clearable
                    ></el-date-picker>
                  </el-col>
                </el-form-item>
                <el-form-item label="电话号码">
                  <el-input v-model="form.telphone" clearable></el-input>
                </el-form-item>
                <el-form-item label="余额">
                  <el-input v-model="form.remainderMoney" disabled></el-input>
                </el-form-item>
                <el-form-item label="状态" hidden>
                  <el-input v-model="form.status"></el-input>
                </el-form-item>
              </el-form>
              <div slot="footer" class="dialog-footer">
                <el-button @click="cancleModify">取 消</el-button>
                <el-button type="primary" @click="confirmModify">确 定</el-button>
              </div>
            </el-dialog>&nbsp;
            <el-button
              size="mini"
              type="danger"
              @click="handleDelete(scope.$index, scope.row,tableData)"
              icon="el-icon-delete"
            >删除</el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-row>
    <el-row>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-sizes="[5, 10]"
        :page-size="5"
        layout="total, sizes, prev, pager, next, jumper"
        v-bind:total="total"
      ></el-pagination>
    </el-row>
  </div>
</template>

<script>
import tableRender from "../../../api/tableRender.js";
export default {
  name: "manager",
  data() {
    return {
      tableData: [
        {
          accountId: "",
          accountName: "",
          name: "",
          gender: "",
          birth: "",
          telephone: "",
          remainderMoney: ""
        }
      ],
      currentPage: 1,
      total: 0,
      pageSize: "",
      loading: true,
      loadingText: "加载中……",
      accountName: "",
      name: "",
      telphone: "",
      idNumber: "",
      form: {
        accountId: "",
        accountName: "",
        name: "",
        gender: "",
        birth: "",
        telphone: "",
        remainderMoney: "",
        password: ""
      },
      formAdd: {
        accountId: "",
        accountName: "",
        name: "",
        gender: "",
        birth: "",
        telphone: "",
        remainderMoney: "",
        password: ""
      },
      dialogFormVisible: false,
      addDialog: false,
      rules: {
        accountName: [
          { required: true, message: "请输入账户名", trigger: "blur" }
        ],
        password: [{ required: true, message: "请输入密码", trigger: "blur" }],
        name: [
          {
            required: true,
            message: "请输入姓名",
            trigger: "blur"
          },
          {
            min: 2,
            max: 15,
            message: "请输入正确的真实姓名，长度在2-15个字符",
            trigger: "blur"
          }
        ],
        idNumber: [
          { required: true, message: "请输入身份证信息", trigger: "blur" },
          {
            pattern: /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/,
            message: "请输入正确的身份证号码",
            trigger: "blur"
          }
        ],
        telphone: [
          {
            required: true,
            message: "请输入电话号码",
            trigger: "blur"
          },
          {
            type:"number",
            message:'请输入正确的电话号码',
            trigger:'blur'
          }
        ]
      }
    };
  },
  methods: {
    customerTable() {
      let that = this;
      tableRender.findAllUsers(1, 5, 1, null, null, null).then(function(res) {
        var data = res.data.data;
        var total = res.data.count;
        window.console.log("total==>", total);
        window.console.log(data);
        that.tableData = data;
        that.total = total;
        that.idNumber = res.data.data.idNumber;
        that.pageSize = 5;
        that.loading = false;
      });
    },
    handleCurrentChange(val) {
      let that = this;
      that.loading = true;
      window.console.log("当前页：", val);
      window.console.log("页面大小:", that.pageSize);
      tableRender
        .findAllUsers(1, that.pageSize, val, null, null, null)
        .then(function(res) {
          var data = res.data.data;
          var total = res.data.count;
          that.tableData = data;
          that.total = total;
          that.currentPage = val;
          that.loading = false;
        });
    },
    handleSizeChange(pageSize) {
      window.console.log(pageSize);
      let that = this;
      that.loading = true;
      tableRender
        .findAllUsers(1, pageSize, 1, null, null, null)
        .then(function(res) {
          var data = res.data.data;
          var total = res.data.count;
          that.tableData = data;
          that.total = total;
          that.pageSize = pageSize;
          that.loading = false;
        });
    },
    search() {
      let that = this;
      that.loading = true;
      tableRender
        .findAllUsers(
          1,
          that.pageSize,
          1,
          this.accountName,
          this.name,
          this.telphone
        )
        .then(function(res) {
          var data = res.data.data;
          var total = res.data.count;
          that.tableData = data;
          that.total = total;
          that.loading = false;
        });
    },
    add() {
      window.console.log("add");
      this.addDialog = true;
    },
    confirmAdd(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          window.console.log("确认添加");
          let that = this;
          tableRender.addUser(this.formAdd, "3").then(function(res) {
            window.console.log("res==>", res);
            that.loadingText="新增中……"
            that.customerTable();
            if (res.data > 0) {
              window.console.log("添加成功");
              that.$message({
                message: "添加成功",
                type: "success"
              });
              that.addDialog = false;
            } else {
              that.$message.error('添加失败');
            }
          });
        } else {
          return false;
        }
      });
    },
    cancleAdd() {
      window.console.log("取消");
      this.addDialog = false;
    },
    showIdNumber(index, row) {
      let that = this;
      that.idNumber = row.idNumber;
    },
    handleEdit(index, row) {
      window.console.log("index==>", index);
      window.console.log("row==>", row);
      this.dialogFormVisible = true;
      this.form.accountId = row.accountId;
      this.form.accountName = row.accountName;
      this.form.birth = row.birth;
      this.form.gender = row.gender;
      this.form.idNumber = row.idNumber;
      this.form.name = row.name;
      this.form.remainderMoney = row.remainderMoney;
      this.form.telphone = row.telephone;
      this.form.password = row.password;
      this.form.birth = row.birth;
    },
    confirmModify() {
      window.console.log("确认修改");
      let that = this;
      tableRender.modifyUsers(this.form).then(function() {
        that.loading = true;
        that.loadingText="修改中……"
        that.handleCurrentChange(that.currentPage);
        that.dialogFormVisible = false;
      });
      that.$message({
        message: "修改成功",
        type: "success"
      });
    },
    cancleModify() {
      window.console.log("取消修改");
      this.dialogFormVisible = false;
    },
    handleDelete(index, row,rows) {
      window.console.log("index==>", index);
      window.console.log("row==>", row);
      let that = this;
      this.$confirm("确认删除", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(function() {
          that.form.accountId = row.accountId;
          that.form.status = 1;
          that.form.accountName = row.accountName;
          that.form.gender = row.gender;
          that.form.birth = row.birth;
          that.form.telphone = row.telphone;
          that.form.remainderMoney = row.remainderMoney;
          that.form.password = row.password;
          that.form.name = row.name;
          window.console.log(that.form);
          tableRender.modifyUsers(that.form).then(function() {
            rows.splice(index,1);
          });
          that.$message({
            type: "success",
            message: "删除成功"
          });
        })
        .catch(function() {
          that.$message({
            type: "info",
            message: "取消删除"
          });
        });
    }
  },
  mounted() {
    this.customerTable();
  }
};
</script>

<style>
</style>
