<template>
  <el-container>
    <el-header>
      <el-row :gutter="20">
        <el-col :span="4">
          <div class="grid-content">
            <span>
              <a href="#">四川民宿电商平台</a>
            </span>
          </div>
        </el-col>
        <el-col :span="16">
          <div class="grid-content"></div>
        </el-col>
        <el-col :span="4">
          <div class="grid-content">
            <span>
              <a href="javascript:void(0)" @click="modifyInfo" :v-text="accountName">{{accountName}}</a>
            </span>
            <span>
              <a href="javascript:void(0)" v-on:click="loginOutClick()">退出</a>
            </span>
          </div>
        </el-col>
      </el-row>
    </el-header>
    <el-container style="height:870px">
      <el-aside width="202px" style="border-right: 1px lightgrey solid;">
        <el-menu
          default-active="2"
          class="el-menu-vertical-demo"
          @open="handleOpen"
          @close="handleClose"
          background-color="#4682B4"
          text-color="#fff"
          active-text-color="#ffd04b"
          style="height:870px"
          router
        >
          <el-submenu
            :key="menu.resourceId"
            v-bind:index="menu.url"
            v-for="menu in a"
            v-if="menu.children!==undefined && menu.children.length!=0"
          >
            <template slot="title">
              <i class="fas" v-bind:class="menu.icon" style="'#fff'"></i>&nbsp;
              <span>{{menu.resourceName}}</span>
            </template>
            <el-menu-item
              :key="child.resourceId"
              v-for="child in menu.children"
              v-bind:index="child.url"
              @click="addTab(editableTabsValue2,child.resourceName,child.url)"
            >
              <i class="fas" v-bind:class="child.icon"></i>
              &nbsp;
              {{child.resourceName}}
            </el-menu-item>
          </el-submenu>
          <el-menu-item
            :key="menu.resourceId"
            v-for="menu in a"
            v-bind:name="menu.resourceName"
            v-if="menu.children===undefined || menu.children.length===0"
            v-bind:index="menu.url"
            @click="addTab(editableTabsValue2,menu.resourceName,menu.url)"
          >
            <i class="fas" v-bind:class="menu.icon"></i>
            &nbsp;
            {{menu.resourceName}}
          </el-menu-item>
        </el-menu>
      </el-aside>
      <el-container>
        <el-main style="background-color:#F6F8FA">
          <el-tabs
            v-model="editableTabsValue2"
            type="border-card"
            closable
            @tab-remove="removeTab"
            @tab-click="tabClick"
            style="height:800px;background-color:#F6F8FA"
          >
            <el-tab-pane
              v-for="(item) in editableTabs2"
              :key="item.name"
              :label="item.title"
              :name="item.name"
              :url="item.url"
            >
              {{item.content}}
              <router-view/>
            </el-tab-pane>
          </el-tabs>
        </el-main>
      </el-container>
    </el-container>
    <el-footer>&copy;2019 四川民宿电商平台后台管理系统</el-footer>
    <el-dialog title="个人信息修改" id="modifyInfo" :visible.sync="infoModifyDialog">
      <el-form :model="infoModifyForm">
          <el-row :gutter="80">
            <el-col :span="7">
              <el-form-item label="账户id">
                  <el-input v-model="infoModifyForm.accountId" disabled></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="7">
              <el-form-item label="账户名">
                  <el-input v-model="infoModifyForm.accountName" clearable></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="7">
              <el-form-item label="密码">
                  <el-input v-model="infoModifyForm.password" clearable></el-input>
                </el-form-item>
            </el-col>
          </el-row>
          <el-row :gutter="80">
            <el-col :span="7">
              <el-form-item label="真实姓名">
                  <el-input v-model="infoModifyForm.name" disabled></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="7">
              <el-form-item label="性别">
                  <el-input v-model="infoModifyForm.gender" disabled></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="7">
              <el-form-item label="出生日期">
                  <el-date-picker
                      type="date"
                      placeholder="选择日期"
                      v-model="infoModifyForm.birth"
                      style="width: 100%;"
                      clearable
                    ></el-date-picker>
                </el-form-item>
            </el-col>
          </el-row>
          <el-row :gutter="80">
            <el-col :span="7">
              <el-form-item label="身份证号码">
                  <el-input v-model="infoModifyForm.idNumber" disabled></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="7">
              <el-form-item label="电话号码">
                  <el-input v-model="infoModifyForm.telephone" clearable></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="7">
              <el-form-item label="余额">
                  <el-input v-model="infoModifyForm.remainderMoney" disabled></el-input>
                </el-form-item>
            </el-col>
          </el-row>
        </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="infoModifyDialog = false">取 消</el-button>
        <el-button type="primary" @click="confirmModifyInfo">确 定</el-button>
      </div>
    </el-dialog>
  </el-container>
</template>

<script>
import menu from "../api/menu.js";
import logout from "../api/logout.js";
import login from "../api/login.js";
import tableRender from "../api/tableRender.js"
import { setTimeout } from "timers";
export default {
  name: "index",
  data() {
    return {
      a: null,
      editableTabsValue2: "2",
      editableTabs2: [
        {
          title: "首页",
          name: "1",
          url: "/index"
        }
      ],
      tabIndex: 1,
      accountName: "",
      infoModifyDialog: false,
      infoModifyForm: {
        accountId: "",
        accountName: "",
        gender: "",
        idNumber: "",
        name: "",
        password: "",
        remainderMoney: "",
        telephone: "",
        birth: ""
      }
    };
  },
  methods: {
    handleOpen(key, keyPath) {
      window.console.log(key, keyPath);
    },
    handleClose(key, keyPath) {
      window.console.log(key, keyPath);
    },
    getMenuTree() {
      let that = this;
      menu.getMenuTree().then(function(res) {
       // window.console.log(res.data);
        if (res.data.length == 0) {
          that.$alert("登录信息失效，请重新登录", "提醒", {
            confirmButtonText: "确定",
            callback: function() {
              that.$message({
                type: "info",
                message: "跳转中，请稍后"
              }),
                setTimeout(function() {
                  window.location.href = "http://localhost/login.html";
                }, "1000");
            }
          });
        }
        that.a = res.data;
        window.console.log(that.a);
      });
    },
    getAccount() {
      let that = this;
      login.getAccount().then(function(res) {
        window.console.log(res);
        that.accountName = res.data.accountName;
      });
    },
    loginOutClick() {
      window.console.log("退出");
      let that = this;
      that
        .$confirm("确定退出？", "退出", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        })
        .then(() => {
         
          that.$message({
            type: "info",
            message: "退出成功,将跳转至登录页面"
          });
          logout.logoutClick().then(function(res) {
            window.console.log(res)
          });
          setTimeout(function() {
            window.location.href = "http://localhost/login.html";
          }, "1000");
        })
        .catch(() => {
          that.$message({
            type: "info",
            message: "已取消退出"
          });
        });
    },
    addTab(targetName, name, url) {
      window.console.log("editableTabs2==>", this.editableTabs2);
      for (var i = 0; i < this.editableTabs2.length; i++) {
        if (this.editableTabs2[i].url === url) {
          window.console.log("exist");
          let thisTab = this.editableTabs2[i];
          window.console.log("thisTab==>", thisTab);
          let activeName = this.editableTabsValue2;
          window.console.log("activeName==>", activeName);
          activeName = thisTab.name;
          this.$router.push({
            path: url
          });
        } else {
          let newTabName = ++this.tabIndex + "";
          this.editableTabs2.push({
            title: name,
            name: newTabName,
            url: url
          });
          this.editableTabsValue2 = newTabName;
        }
        break;
      }
    },
    tabClick(tab) {
      window.console.log("tab", tab.$attrs.url);
      this.$router.push({
        path: tab.$attrs.url
      });
    },
    removeTab(targetName) {
      window.console.log("remove");
      let tabs = this.editableTabs2;
      let activeName = this.editableTabsValue2;
      if (activeName === targetName) {
        tabs.forEach((tab, index) => {
          if (tab.name === targetName) {
            let nextTab = tabs[index + 1] || tabs[index - 1];
            window.console.log("nextTab==>", nextTab);
            window.console.log(nextTab.url);
            if (nextTab) {
              activeName = nextTab.name;
              this.$router.push({
                path: nextTab.url
              });
              nextTab--;
            }
          }
        });
      }
      this.editableTabsValue2 = activeName;
      this.editableTabs2 = tabs.filter(tab => tab.name !== targetName);
    },
    modifyInfo() {
      window.console.log(this.accountName);
      this.infoModifyDialog = true;
      let that=this;
      login.getAccount().then(function(res){
        that.infoModifyForm=res.data;
      })
    },
    confirmModifyInfo() {
      this.infoModifyDialog = false;
      let that=this;
      tableRender.modifyUsers(this.infoModifyForm).then(function(res){
        if(res.data<=0){
          that.$message.error("修改失败");
        }else{
          that.$message({
            type:"success",
            message:"修改成功"
          })
        }
      })

    }
  },
  mounted() {
    this.getMenuTree();
    this.getAccount();
  }
};
</script>

<style>
* {
  text-decoration: none;
  list-style: none;
}

a {
  color: #fff;
}

.el-submenu a {
  color: black;
}

.el-header,
.el-footer {
  background-color: #36648b;
  color: #fff;
  text-align: center;
  line-height: 60px;
}

.el-header {
  text-align: left;
  margin: 0;
  padding: 0;
  width: 100%;
}

.el-footer {
  width: 100%;
}

.el-header span {
  margin-left: 30px;
}

.el-aside {
  color: #333;
  text-align: center;
}

.el-main {
  color: #333;
  text-align: center;
}

body > .el-container {
  margin-bottom: 40px;
}

.el-container:nth-child(5) .el-aside,
.el-container:nth-child(6) .el-aside {
  line-height: 260px;
}

.el-container:nth-child(7) .el-aside {
  line-height: 320px;
}

.el-row {
  margin-bottom: 20px;
}

.el-tabs__nav {
  line-height: 40px;
}

.el-col {
  border-radius: 4px;
}

.bg-purple-dark {
  background: #99a9bf;
}

.bg-purple {
  background: #d3dce6;
}

.bg-purple-light {
  background: #e5e9f2;
}

.grid-content {
  border-radius: 4px;
  min-height: 36px;
}

.row-bg {
  padding: 10px 0;
  background-color: #f9fafc;
}
</style>
